@import "color";

.purchase-wrap {padding: 0 1.5rem 1.5rem; border: 1px solid #e7e7e8;
    .purchase-header {border-bottom: 1px solid #666;
        .tlt {font-size: 1.2rem;}
    }
    .purchase-body {
        .tlt {font-size: 1rem; color: #666;}
        .lecture-info-wrap {
            .tlt {color: #111;}
        }
    }
}

.inner {
    .user-info-wrap {padding: 1.5rem; border: 1px solid #e7e7e8;
        .user-info-title-wrap {border-bottom: 1px solid #666; padding-bottom: 1.5rem;}
        .tlt {font-size: 1.2rem;}
    }
}